import stringCss from './stringCss.js'
//é¢å‘å¯¹è±¡
const player = {
  id: undefined,
  time: 100,
  ui: {
    demo: document.querySelector('#demo'),
    demo2: document.querySelector('#demo2')
  },
  //è¡¨é©±åŠ¨ç¼–ç¨‹
  events: {
    '#btnPause': 'pause',
    '#btnPlay': 'play',
    '#btnSlow': 'slow',
    '#btnNormal': 'normal',
    '#btnFast': 'fast',
    '#btnReload':'reload'
  },
  n: 1,
  init: () => {
    // åŒæ—¶æ“ä½œdemoå’Œdemo2ï¼Œå±•ç¤ºæ–‡æœ¬å’Œæ¸²æŸ“æ ·å¼
    player.ui.demo.innerText = stringCss.substring(0, player.n)
    player.ui.demo2.innerHTML = stringCss.substring(0, player.n)
    player.bindEvents()
    player.play()
  },
  //ç»‘å®šæŒ‰é’®äº‹ä»¶
  bindEvents: () => {
    for (let key in player.events) {
      if (player.events.hasOwnProperty(key)) {
        const value = player.events[key] // pause / play / slow
        document.querySelector(key).onclick = player[value] //è¿™æ˜¯ä¸€ä¸ªå‡½æ•°
      }
    }
  },
  run: () => {
    player.n += 1
    if (player.n > stringCss.length) {
      return window.clearInterval(player.id)
    }
    player.ui.demo.innerText = stringCss.substring(0, player.n)
    player.ui.demo2.innerHTML = stringCss.substring(0, player.n)
    player.ui.demo.scrollTop = player.ui.demo.scrollHeight //é¡µé¢æ»šåŠ¨ä½ç½®ä¸ºæ»šåŠ¨æ¡é«˜åº¦ï¼ˆé¡µé¢æ‹‰åº•çŠ¶æ€ï¼‰
  },
  play: () => {
    window.clearInterval(player.id)//æ’­æ”¾å³é”€æ¯å®šæ—¶å™¨åé‡æ–°å®‰è£…å®šæ—¶å™¨
    player.id = setInterval(player.run, player.time)
  },
  pause: () => {
    window.clearInterval(player.id)
  },
  //å˜é€Ÿï¼Œå…ˆé”€æ¯åé‡æ–°å®‰è£…å®šæ—¶å™¨
  slow: () => {
    player.pause() 
    player.time = 300
    player.play()
  },
  normal: () => {
    player.pause()
    player.time = 100
    player.play()
  },
  fast: () => {
    player.pause()
    player.time = 0
    player.play()
  },
  reload: () => {
    player.pause()
    player.time = 100
    player.n = 1
    player.play()
  }
}

player.init()